.fixtures {
    grid-area: fx;
    max-height: 100%;
    height:88vh;
    overflow-y: auto;
}

.sidemenu {
  grid-area: sm;
  max-height: 100%;
  height:88vh;
  overflow: auto;
}

.body {
  grid-area: mn;
  max-height: 88vh;
  overflow-y: hidden;
}

.chat_wrapper {
    display: grid;
    grid-template-columns: repeat(9, 1fr);
    grid-template-areas:
      "sm sm mn mn mn mn fx fx fx"
}
* {-webkit-box-sizing: border-box;box-sizing: border-box;}

.chat_wrapper {
    border-radius: 5px;
    max-width: 1500px;
    margin: 0 auto;
}

.chat_wrapper > div {
    border-radius: 5px;
}



