h1 {
  margin: 0;
  border: 0;
}

.ChatHeader {
  height: 4vh;
}

.MessageBox {
  height: 78vh;
  overflow-y:auto;
  scroll-behavior: smooth;
  border-top: 2px solid black;
  border-bottom: 2px solid black;
}

.MessageBuilder {
  width: 80%;
  height: 100%;
}

input[type=text] {
    width: 80%;
    height: 100%;
    margin: 8px 0;
    font-size: 2vh;
    padding: 1.5vh 0;
    margin: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.SendButton {
    background-color: #4CAF50;
    border-color: #4CAF50;
    color: white;
    width: 20%;
    height: 100%;
    font-size: 2vh;
    padding: 1.5vh 0;
    margin: 0;
    text-decoration: none;
    cursor: pointer;
}

.ChatPanel {
  height: 100%;
  border-bottom: 2px solid black;
  display: block;
}
* {-webkit-box-sizing: border-box;box-sizing: border-box;}



