
.wrapper {
  border: 0px;
  padding: 0px;
  max-width: 1500px;;
  max-height: 100%;
  height:100vh;
  margin: 0 auto;
}


.wrapper > div {
  overflow-y: auto;
  border-radius: 5px;
  padding: 0em;
}

.header {
  padding: 2px;
  max-height: 12vh;
  background-color: #333333;
}

.main {
  max-height:88vh;
  overflow-y:hidden;
  scroll-behavior: smooth;
}



